footer {
  background-color: rgb(122, 203, 122);
  padding: 10px;
  margin-top: 50px;
}

.footerContainer {
  display: flex;
  justify-content: center;
  text-align: start;
  color: rgb(67, 66, 66);
}

.footer {
  display: flex;
  justify-content: space-around;
  padding: 30px;
  margin: 20px;
}

.leftFooter {
  flex: 1;
  align-items: start;
}

.logoFooter {
  height: 80px;
  height: 80px;
  margin-left: -10px;
}

.logoText {
  height: 70px;
  height: 70px;
  margin-left: -20px;
}


.socialIcons {
  display: flex;
}

.socialIcon {
  width: 25px;
  height: 25px;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 5px;
  cursor: pointer;
}

.facebook:hover {
  border-radius: 50%;
  background-color: #4f82f0;
}

.instagram:hover {
  border-radius: 20%;
  background-color: #e4405f;
}

.twitter:hover {
  border-radius: 20%;
  background-color: rgb(255, 255, 255);
}

.linkedin:hover {
  border-radius: 20%;
  background-color: #6e94e5;
}

.youtube:hover {
  border-radius: 20%;
  background-color: #f10206;
}

.list {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
  flex-wrap: wrap;
}

li {
  width: 50%;
  margin-bottom: 5px;
  font-size: small;
}

.rightFooter {
  padding: 0;
  flex: 1;
}

hr {
  width: 50%;
  border: 0;
  height: 1px;
  background-color: #abdaa0;
  margin: 10px auto;
}

.copyWright {
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgb(217, 237, 216);
  padding: 5px 0 15px;
  font-size: 13px;
}

a {
  text-decoration: none;
  color: #2c234d;
  cursor: pointer;
}

@media (max-width: 768px) {
  .footer {
    flex-direction: column;
  }

  .iconFooter {
    display: flex-start;
  }
  
  .rightFooter {
    margin-top: 50px;
  }
}